import './card.styles.css';

const Card = ({ monster: { id, name, email } }) => (
    <div className="card-container">
        <img
            src={`https://robohash.org/${id}?set=set2&size=180x180`}
            height="180px"
            width="180px"
            alt={`Mr. ${name}`}
        />
        <h2>{name}</h2>
        <h3>{email}</h3>
    </div>
);

export default Card;
